.field-date-time {
  margin-top: 2.5rem;
  position: relative;
  width: 100%;
  height: 5rem; }
  .field-date-time input {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0 1rem;
    cursor: pointer; }
  .field-date-time label {
    position: absolute;
    background-color: #FFF;
    color: #808080;
    cursor: pointer;
    height: 100%;
    width: 100%;
    z-index: 1;
    padding: 0 2rem;
    display: flex;
    align-items: center;
    transition: 1s; }
  .field-date-time label:hover {
    width: 40%;
    margin-left: 45%;
    color: #0da6f3;
    font-size: 1.2rem;
    font-weight: 700;
    background-color: transparent;
    padding: 0; }
    @media (min-width: 480px) {
      .field-date-time label:hover {
        font-size: 1.5rem; } }
  .field-date-time .hover-border::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    height: 4px;
    width: 0;
    background-color: #0da6f3;
    transition: 1s;
    z-index: 1; }
  .field-date-time label:hover ~ .hover-border::before,
  .field-date-time label:hover ~ .hover-border-2::before {
    width: 100%; }
  .field-date-time .hover-border::after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 4px;
    height: 0;
    background-color: #0da6f3;
    z-index: 1;
    transition: 1s; }
  .field-date-time label:hover ~ .hover-border::after,
  .field-date-time label:hover ~ .hover-border-2::after {
    height: 100%; }
  .field-date-time .hover-border-2::before {
    position: absolute;
    content: "";
    right: 0;
    bottom: 0;
    height: 4px;
    width: 0;
    background-color: #0da6f3;
    z-index: 1;
    transition: 1s; }
  .field-date-time .hover-border-2::after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 0;
    height: 0;
    width: 4px;
    background-color: #0da6f3;
    z-index: 1;
    transition: 1s; }
  .field-date-time input.selected ~ .hover-border::after,
  .field-date-time input.selected ~ .hover-border-2::after,
  .field-date-time input:focus ~ .hover-border::after,
  .field-date-time input:focus ~ .hover-border-2::after,
  .field-date-time input.correct ~ .hover-border::after,
  .field-date-time input.correct ~ .hover-border-2::after {
    height: 100%; }
  .field-date-time input.selected ~ label {
    font-weight: 700; }
  .field-date-time input.selected ~ .hover-border::before,
  .field-date-time input.selected ~ .hover-border-2::before,
  .field-date-time input:focus ~ .hover-border::before,
  .field-date-time input:focus ~ .hover-border-2::before,
  .field-date-time input.correct ~ .hover-border::before,
  .field-date-time input.correct ~ .hover-border-2::before {
    width: 100%; }
  .field-date-time input.selected ~ label,
  .field-date-time input:focus ~ label {
    width: 40%;
    margin-left: 45%;
    color: #0da6f3;
    font-size: 1.2rem;
    font-weight: 700;
    background-color: transparent;
    padding: 0; }
    @media (min-width: 480px) {
      .field-date-time input.selected ~ label,
      .field-date-time input:focus ~ label {
        font-size: 1.5rem; } }
  .field-date-time input.correct ~ .hover-border::before,
  .field-date-time input.correct ~ .hover-border-2::before,
  .field-date-time input.correct ~ .hover-border::after,
  .field-date-time input.correct ~ .hover-border-2::after {
