  .field-date-time input.wrong ~ .hover-border::before,
  .field-date-time input.wrong ~ .hover-border-2::before,
  .field-date-time input.wrong ~ .hover-border::after,
  .field-date-time input.wrong ~ .hover-border-2::after {
    background-color: #F22439; }
  .field-date-time input.wrong ~ label {
    color: #F22439 !important; }
  .field-date-time input.chosen ~ label {
