@mixin nineHundred {
    @media(min-width: 900px) {
        @content;
    }
}

@mixin fivefifty {
    @media(min-width: 550px) {
        @content;
    }
}

@mixin pc {
    @media (min-width: 1048px) {
        @content;
    }
}

@mixin tablet {
    @media(min-width: 768px) {
        @content;
    }
}

@mixin mobile {
    @media(min-width: 480px) {
        @content;
    }
}